.swiper {
	width: 100%;
	height: 100%;
	display: block;
}

.swiper-pagination-bullet {
	width: 15px;
	height: 15px;
	text-align: center;
	line-height: 20px;
	font-size: 12px;
	color: #bbbbbb;
	opacity: 1;
	background: rgba(187, 187, 187, 0.4);
}

.swiper-pagination-bullet-active {
	color: #fff;
	background: #ffffffae;
}
